.tui-pagination {
    margin-top: 0px;
    padding-bottom: 12px;
}

.tui-pagination .tui-page-btn {
    width: 40px;
    height: 40px;
    padding-top: 13px;
    text-align: center; 
    justify-content: center;

    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;

    border: 1px solid white;

    &:hover, 
    &:focus {
        background-color: #FF6B08;
        border-radius: 5px;
    }
}

.tui-pagination .tui-is-selected, .tui-pagination strong {
    background-color: #FF6B08;
    border-color: #FF6B08;
    border-radius: 5px;
    cursor: pointer;
    
}

